/* src/Header.css */
.options-enter {
    max-height: 0;
    opacity: 0;
  }
  .options-enter-active {
    max-height: 500px;
    opacity: 1;
    transition: max-height 300ms ease-in-out, opacity 300ms ease-in-out;
  }
  .options-exit {
    max-height: 500px;
    opacity: 1;
  }
  .options-exit-active {
    max-height: 0;
    opacity: 0;
    transition: max-height 300ms ease-in-out, opacity 300ms ease-in-out;
  }
  
  .sub-options-enter {
    max-height: 0;
    opacity: 0;
  }
  .sub-options-enter-active {
    max-height: 500px;
    opacity: 1;
    transition: max-height 300ms ease-in-out, opacity 300ms ease-in-out;
  }
  .sub-options-exit {
    max-height: 500px;
    opacity: 1;
  }
  .sub-options-exit-active {
    max-height: 0;
    opacity: 0;
    transition: max-height 300ms ease-in-out, opacity 300ms ease-in-out;
  }
  